import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import AboutMe from '@/components/AboutMe'
import Deliverables from '@/components/Deliverables'
import Activity from '@/components/Activity'
import SNS from '@/components/SNS'
import Pair from '@/components/product/Pair'
import Accelerometer from '@/components/product/Accelerometer'
import Locater from '@/components/product/Locater'
import Dice from '@/components/product/Dice'
import RandomPrefecture from '@/components/product/RandomPrefecture'
import TravelingTool from '@/components/product/TravelingTool'
import NumGame from '@/components/product/NumGame'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/about',
      name: 'AboutMe',
      component: AboutMe
    },
    {
      path: '/del',
      name: 'Deliverables',
      component: Deliverables
    },
    {
      path: '/act',
      name: 'Activity',
      component: Activity
    },
    {
      path: '/sns',
      name: 'SNS',
      component: SNS
    },
    {
      path: '/pair',
      name: 'Pair',
      component: Pair
    },
    {
      path: '/accelerometer',
      name: 'Accelerometer',
      component: Accelerometer
    },
    {
      path: '/locater',
      name: 'Locater',
      component: Locater
    },
    {
      path: '/dice',
      name: 'Dice',
      component: Dice
    },
    {
      path: '/randomprefecture',
      name: 'RandomPrefecture',
      component: RandomPrefecture
    },
    {
      path: '/travelingtool',
      name: 'TravelingTool',
      component: TravelingTool
    },
    {
      path: '/numgame',
      name: 'NumGame',
      component: NumGame
    }
  ]
})



