    return {
      name: 'App'
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #fff;
  margin-top: 40px;
}

a {
  text-decoration: none;
  color: #fff;
}

.logo {
  width: 50px;
  height: 50px;
}
</style>



